import React, { Suspense } from "react";

// Importação de componentes
import ViewInicial from "./ViewComponents/View_Inicial";
import ViewCredito from "./ViewComponents/View_Credito";
import ViewCompartilhar from "./ViewComponents/View_Compartilhar";

import ViewInstrucao from "./ViewComponents/View_Instrucao";
import ViewModos from "./ViewComponents/View_Modos";
// import ViewJogo from "./ViewJogo";
import View from "./View";
import View2 from "./View2";
import View1 from "./View1";
import Nav from "./Nav";

// Conteudo ilustrado das intruções
import nav1 from "../Assets/instrucoes/2_SelecaoSwitch.gif";
import nav2 from "../Assets/instrucoes/2_SelecaoTab.gif";

import acao1 from "../Assets/instrucoes/3_Letras.gif";

import obj1 from "../Assets/instrucoes/1_dicaContexto.gif";

import lis1 from "../Assets/instrucoes/4_Acerto.gif";
import lis2 from "../Assets/instrucoes/4_Lista.gif";
import mob1 from "../Assets/instrucoes/4_Lista_mob.gif";




// Componentes de roteamento
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

// Componentes Front-end
import "../Style/Diretrizes.scss";

// Controladores
import ControleJogo from "./Controllers/ControleJogo";

/**
 * @param {Null} Null Null
 * @description Classe geradora da função app
 * @constructor
 */

//  const JogoInstance = React.lazy(() => import('./Controllers/ControleJogo'));
const ViewJogo = React.lazy(() => import("./ViewJogo"));
const JogoInstance = new ControleJogo();
console.log("Instancia do JOGO::::", JogoInstance);

export default function App() {
  // ROTEAMENTO - colocar controle de estados da aplicação
  return (
    <Router basename="/app/cruzadinhasAedes/">
      {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
      <Switch>
        <Route exact path="/">
          <Tela_Inicial />
        </Route>
        <Route exact path="/creditos">
          <Tela_Creditos />
        </Route>
        <Route exact path="/compartilhar">
          <Tela_Compartilhar />
        </Route>
        <Route exact path="/instrucao-0">
          <Tela_Instrucao0 />
        </Route>
        <Route exact path="/instrucao-1">
          <Tela_Instrucao1 />
        </Route>
        <Route exact path="/instrucao-2">
          <Tela_Instrucao2 />
        </Route>
        <Route exact path="/instrucao-3">
          <Tela_Instrucao3 />
        </Route>
        <Route exact path="/preparado">
          <Tela_Preparado />
        </Route>
        <Route exact path="/modo">
          <Tela_Modos />
        </Route>

        <Route exact path="/jogo">
          <Jogo />
        </Route>
      </Switch>
    </Router>
  );

  function Jogo() {
    return (
      <div className="d-flex flex-row">
        <Nav className="flex-pill" />
        <Suspense
          fallback={
            <div className="spinner-border text-secondary" role="status">
              <span className="visually-hidden">Loading...</span>
            </div>
          }
        >
          <ViewJogo Jogo={JogoInstance} />
        </Suspense>
      </div>
    );
  }

  function Tela_Inicial() {
    return (
      <div>
        <View conteudo={<ViewInicial />} />
      </div>
    );
  }
  function Tela_Creditos() {
    return (
      <div>
        <View1 conteudo={<ViewCredito back={true} />} />
      </div>
    );
  }

  function Tela_Compartilhar() {
    return (
      <div>
        <View1 conteudo={<ViewCompartilhar back={true} />} />
      </div>
    );
  }


  function Tela_Instrucao0() {
    const instrucao = (
      <div class="d-flex flex-column w-100 h-100">
        <div class="flex-grow-1 d-flex  flex-row justify-content-center align-items-center pt-3">
          <div class="flex-fill text-center"><img className="img-col1 img-fluid" src={obj1} alt="nevegue clicando nas palavras do tabuleiro" /></div>
        </div>
        <div class="flex-fill d-flex justify-content-center align-items-end fs-bold ">
            <p>Você precisa usar os contextos para encontrar as palavras, quando precisar de ajuda extra clique em dica !</p>  
        </div>
      </div>
    );

    const elemento = ViewInstrucao(instrucao);
    const prev = "/";
    const next = "/instrucao-1";
    return (
      <div className="d-flex flex-row">
        <Nav className="flex-pill" />
        <View2
          className="flex-pill"
          conteudo={elemento}
          prev={prev}
          next={next}
          titulo={"Objetivo"}
        />
      </div>
    );
  }

  function Tela_Instrucao1() {
    const instrucao = (
      <div class="d-flex flex-column w-100 h-100">
        
        <div class="flex-grow-1 d-flex  flex-row align-items-center pt-3">
          <div class="flex-fill"><img className="img-col2 img-fluid" src={nav1} alt="nevegue clicando nas palavras do tabuleiro" /></div>
          <div class="flex-fill"><img className="img-col2 img-fluid" src={nav2} alt="nevegue clicando nas setas do teclado" /></div>
        </div>
        <div class="flex-fill d-flex justify-content-center align-items-end fs-bold ">
            <p>Neste jogo você pode navegar entre as palavras clicando nas palavras no tabuleiro ou atravês das setas do teclado virtual !</p>  
        </div>
      </div>
    );

    const elemento = ViewInstrucao(instrucao);
    const prev = "/instrucao-0";
    const next = "/instrucao-2";
    return (
      <div className="d-flex flex-row">
        <Nav className="flex-pill" />
        <View2
          className="flex-pill"
          conteudo={elemento}
          prev={prev}
          next={next}
          titulo={"Navegação"}
        />
      </div>
    );
  }
  function Tela_Instrucao2() {
    const instrucao = (
      <div class="d-flex flex-column w-100 h-100 ">
        
        <div class="flex-grow-1 d-flex  flex-row justify-content-center align-items-center pt-3">
          <div class="flex-fill text-center"><img className="img-col1 img-fluid" src={acao1} alt="nevegue clicando nas palavras do tabuleiro" /></div>
        </div>
        <div class="flex-fill d-flex justify-content-center align-items-end fs-bold ">
            <p>Em todas as plataformas você deve fazer uso do teclado virtual para preencher as lacunas !</p>
            </div>
      </div>
    );

    const elemento = ViewInstrucao(instrucao);
    const prev = "/instrucao-1";
    const next = "/instrucao-3";
    return (
      <div className="d-flex flex-row">
        <Nav className="flex-pill" />
        <View2
          conteudo={elemento}
          prev={prev}
          next={next}
          titulo={"Preencher"}
        />
      </div>
    );
  }

  function Tela_Instrucao3() {
    let instrucao
    if (window.screen.width < 768) {
       instrucao = (
        <div class="d-flex flex-column w-100 h-100">
          
          <div class="flex-grow-1 d-flex  flex-row justify-content-center align-items-center pt-3">
            <div class="flex-fill text-center"><img className="img-col2 img-fluid" src={lis1} alt="nevegue clicando nas palavras do tabuleiro" /></div>

            <div class="flex-fill text-center"><img className="img-col2 img-fluid" src={mob1} alt="nevegue clicando nas palavras do tabuleiro" /></div>
          </div>
          <div class="flex-fill d-flex justify-content-center align-items-end fs-bold ">
              <p>Você pode revisar as palavras através da <i>lista de palavras</i> clicando no canto superior direito da tela, e quando uma palavra estiver certa será demarcada em verde !</p>
              </div>
        </div>
      );
    }else{
      instrucao = (
        <div class="d-flex flex-column w-100 h-100">
          
          <div class="flex-grow-1 d-flex  flex-row justify-content-center align-items-center pt-3">
            <div class="flex-fill text-center"><img className="img-col2 img-fluid" src={lis1} alt="nevegue clicando nas palavras do tabuleiro" /></div>

            <div class="flex-fill text-center"><img className="img-col2 img-fluid" src={lis2} alt="nevegue clicando nas palavras do tabuleiro" /></div>
          </div>
          <div class="flex-fill d-flex justify-content-center align-items-end fs-bold ">
              <p>Você pode revisar as palavras através da <i>lista de palavras</i>, e quando uma palavra estiver certa será demarcada em verde !</p>
              </div>
        </div>
      );
    }
    

    const elemento = ViewInstrucao(instrucao);
    const prev = "/instrucao-2";
    const next = "/preparado";
    return (
      <div className="d-flex flex-row">
        <Nav className="flex-pill" />
        <View2
          conteudo={elemento}
          prev={prev}
          next={next}
          titulo={"Revisão"}
        />
      </div>
    );
  }


  function Tela_Preparado() {
    const instrucao = "Preparado? Então vamos começar!";
    const acoes = (
      <div>
        <Link to="/modo">
          <button
            type="button"
            className="button-custom inicial bg-white arround-6"
          >
            <span className="material-icons-outlined md-44 align-middle pe-3">
              sports_esports
            </span>
            JOGAR
          </button>
        </Link>
      </div>
    );
    const elemento = ViewModos(instrucao, acoes);
    const prev = "/instrucao-3";
    const next = null;
    return (
      <div className="d-flex flex-row">
        <Nav className="flex-pill" />
        <View2
          conteudo={elemento}
          prev={prev}
          next={next}
          titulo={"Instruções"}
        />
      </div>
    );
  }

  function modo(val) {
    console.log("acionou o modo", val);
    JogoInstance.init(val);
  }

  function Tela_Modos() {
    const instrucao = null;
    const acoes = (
      <div className="d-flex h-75 flex-column">
        <div className="flex-fill w-100 text-center  d-flex justify-content-center align-items-center">
          <Link to="/jogo">
            <button
              type="button"
              onClick={() => modo(1)}
              className="button-custom modo bg-white arround-6"
            >
              
              <span className="float-center align-middle">AEDES</span>
            </button>
          </Link>
        </div>
        <div className="flex-fill w-100 text-center  d-flex justify-content-center align-items-center">
          <Link to="/jogo">
            <button
              type="button"
              onClick={() => modo(2)}
              className="button-custom modo bg-white arround-6"
            >
              <span className="float-center align-middle">DENGUE</span>
            </button>
          </Link>
        </div>
        <div className="flex-fill w-100 text-center  d-flex justify-content-center align-items-center">
          <Link to="/jogo">
            <button
              type="button"
              onClick={() => modo(3)}
              className="button-custom modo bg-white arround-6"
            >
              <span className="float-center align-middle">CHIKUNGUNYA</span>
            </button>
          </Link>
        </div>
        <div className="flex-fill w-100 text-center  d-flex justify-content-center align-items-center">
          <Link to="/jogo">
            <button
              type="button"
              onClick={() => modo(4)}
              className="button-custom modo bg-white arround-6"
            >
              <span className="float-center align-middle">ZIKA</span>
            </button>
          </Link>
        </div>
        <div className="flex-fill w-100 text-center  d-flex justify-content-center align-items-center">
          <Link to="/jogo">
            <button
              type="button"
              onClick={() => modo(5)}
              className="button-custom modo bg-white arround-6"
            >
              <span className="float-center align-middle">LEPTOSPIROSE</span>
            </button>
          </Link>
        </div>
      </div>
    );
    const elemento = ViewModos(instrucao, acoes);
    const prev = null;
    const next = null;
    return (
      <div className="d-flex flex-row">
        <Nav className="flex-pill" />
        <View2
          conteudo={elemento}
          prev={prev}
          next={next}
          titulo={"Escolha o tema"}
        />
      </div>
    );
  }
}
